.ResultBox,
.SummaryBox {
  display: inline-block;
  width: 50%;
}

.Result-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  width: 50%;
  height: 100%;
  border-radius: 25px;
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(hsl(252, 100%, 67%)),
    to(hsl(241, 81%, 54%))
  );
  background-image: -webkit-linear-gradient(
    top,
    hsl(252, 100%, 67%),
    hsl(241, 81%, 54%)
  );
  background-image: -o-linear-gradient(
    top,
    hsl(252, 100%, 67%),
    hsl(241, 81%, 54%)
  );
  background-image: linear-gradient(
    to bottom,
    hsl(252, 100%, 67%),
    hsl(241, 81%, 54%)
  );
  z-index: 1;
}

.Your-result-header {
  margin-top: 30px;
  color: hsl(241, 100%, 89%);
  font-weight: bold;
  font-size: 18px;
  font-family: "Hanken Grotesk", sans-serif;
}

.Result-value {
  font-size: 52px;
  font-weight: 400px;
  color: hsl(0, 0%, 100%);
  font-family: "Hanken Grotesk", sans-serif;
  margin: 0;
}

.Result-circle {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-top: 25px;
  height: 150px;
  width: 150px;
  border-radius: 100%;
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(hsla(256, 72%, 46%, 1)),
    to(hsla(241, 72%, 46%, 0))
  );
  background-image: -webkit-linear-gradient(
    top,
    hsla(256, 72%, 46%, 1),
    hsla(241, 72%, 46%, 0)
  );
  background-image: -o-linear-gradient(
    top,
    hsla(256, 72%, 46%, 1),
    hsla(241, 72%, 46%, 0)
  );
  background-image: linear-gradient(
    to bottom,
    hsla(256, 72%, 46%, 1),
    hsla(241, 72%, 46%, 0)
  );
}

.Result-total-value {
  margin-top: -5px;
  font-size: 13px;
  color: hsla(241, 100%, 89%, 0.5);
  font-weight: bold;
}

.Great-text {
  font-size: 24px;
  margin-top: 22px;
  color: hsl(0, 0%, 100%);
  font-weight: bold;
  font-family: "Hanken Grotesk", sans-serif;
}

.Your-score-text {
  font-family: "Hanken Grotesk", sans-serif;
  font-size: 13px;
  margin-top: 10px;
  color: hsla(241, 100%, 89%, 1);
  text-align: center;
  margin-left: 45px;
  margin-right: 45px;
}

@media (max-width: 767px) {
  .Result-box {
    height: auto;
    width: 100%;
    float: left;
    border-top-right-radius: 0;
    border-bottom-right-radius: 25px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 25px;
  }

  .Your-score-text {
    margin-bottom: 30px;
    width: 70%;
    font-size: 15px;
  }
  .Result-circle {
    height: 120px;
    width: 120px;
  }
  .Your-result-header {
    margin-top: 15px;
  }
}



